.breadcrum-container{
    display: flex;
    flex-direction: row;
    background: white;
    box-shadow: 1px 3px 4px rgba(0,0,0,0.1);
    height: 10vh;
    align-items: center;
    width: 100%;    
    justify-content: flex-end;
}


.breadcrum-cont1{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 5vh;  
  
}

.breadcrum-cont1 img{
    height: 3vh;
    margin: 0 1vh;
}
.breadcrum-cont1 a{
    color: red;
    text-decoration: none;
    margin: 0 1vh;
}