#navbar{
    width: 100%;
    min-height: 100vh;
    display: grid;
    grid-template-rows: 50px calc(100% - 50px);
    grid-template-columns: 240px calc(100% - 240px);
}

#navbar div{
    position: relative;
}
#navbar div .nav-logo-container{
    position: fixed;
    height: 50px;
    width: 240px;
}
#navbar div #nav-logo{
    position: absolute;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
#navbar div #nav-logo img{
    margin-left: 20px;
}
#navbar div #top-nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background-color: rgb(76, 94, 253);
    z-index: 1000;
}

#navbar div #top-nav img{
    position: absolute;
    left: 30px;
    top: 50%;
    transform: translate(-50%,-50%);
    height: 30px;
    color: white;
}

#navbar div #top-nav .button{
    position: absolute;
    right: 30px;
    top: 50%;
    height: 30px;
    transform: translateY(-50%);
}

#navbar div .side-nav-container{
    position: fixed;
    width: 240px;
    height: 100%;
}
#navbar div #side-nav{
    height: 100%;
    width: 100%;
    background-color: #2c2c2c;
}

#navbar div #side-nav .nav-element{
    cursor: pointer;
    height: 40px;
    width: 100%;
    background-color: #2e2e2e;
    color: white;
    position: relative;
}

#navbar div #side-nav .nav-element .nav-element-content{
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
}
#navbar div #side-nav .nav-element .nav-element-content p{
    color: white;
}
#navbar div #side-nav .nav-element .nav-element-content img{
    margin-right: 10px;
    margin-top: 4px;
}

#navbar #body{
    box-sizing: content-box;
    background-color: white;
}
