.container-header{
    width: 100%;
    height: 70px;
    background-color: whitesmoke;
    margin-bottom: 20px;
}
.container-header .form-name{
    position: relative;
    height: 100%;
}
.container-header .form-name h2{
    margin-top: 0;
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
}
